.jumboStyle {
    background: 
        url('./mapofNJ.gif'), 
        url('./southJersey.gif');
    background-repeat: no-repeat;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 100px;
    background-size: auto 100%;
    height: 500px;
}

/* add on click of North Jersey it changes the map to north jersey */
.jumboStyle:hover ~ jumboStyle {
    background: url('./southJersey.gif');

}

.northButton:hover + leftSide {
    background-image: url('./southJersey.gif');
    image: url('./southJersey.gif');
}

